<template>
    <div>
        <TopNav mdl="sms" select="0"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1).这里是短信营销</p>
            <p>2).可以选择短信模板以及手机号码向用户发送短信</p>
        </div>

        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form ref="form" :model="form" label-width="80px" style="width: 600px">
                    <el-form-item label="发送对象">
                        <el-radio-group v-model="form.sendType">
                            <el-radio :label="1">输入手机号</el-radio>
                            <el-radio :label="2">指定用户</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item label="手机号" v-if="form.sendType == 1">
                        <el-input type="textarea" :value="form.tels" resize="none" @input="inputTel"
                                  :autosize="{ minRows: 10, maxRows: 10}" placeholder="仅支持大陆手机号，多个手机号请换行"></el-input>
                        <p>当前人数 {{userNum}} 人</p>
                    </el-form-item>
                    <el-form-item label="用户列表" v-if="form.sendType == 2">
                        <select-user-list @select-user="selectUser"></select-user-list>
                        <template>
                            <el-table size="mini" :data="users" height="250" border style="width: 100%">
                                <el-table-column prop="nickname" label="用户"></el-table-column>
                                <el-table-column prop="mobile" label="手机号"></el-table-column>
                                <el-table-column prop="address" label="操作" width="50">
                                    <template slot-scope="scope">
                                        <el-button type="text" @click="deleteUser(scope.$index)" size="small">删除
                                        </el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                        <p>当前人数 {{userNum}} 人</p>
                    </el-form-item>
                    <el-form-item label="选择模板">
                        <sms-tmp-select @select-tmp="selecrTmp"></sms-tmp-select>

                        <el-input :readonly="true" type="textarea" :value="tmp.text" resize="none"
                                  :autosize="{minRows: 6, maxRows: 6}" placeholder=""></el-input>
                        <!--发送预览-->
                        <!--<el-input :readonly="true" type="textarea" :value="smsYvlan" resize="none"-->
                        <!--:autosize="{ minRows: 6, maxRows: 6}" placeholder="仅支持大陆手机号，多个手机号请换行"></el-input>-->
                        <p>每70个字按一条计费</p>
                    </el-form-item>
                    <el-form-item label="模板参数" v-if="tmp.params">
                        <p v-for="(item) in  tmp.params">参数 {{item.key}}：
                            <el-input  v-model="item.val"  placeholder="请输入内容"></el-input>
                        </p>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">立即发送</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import SelectUserList from "./components/SelectUserList";
    import SmsTmpSelect from "./components/SmsTmpSelect";

    export default {
        name: 'applyList',
        components: {TopNav,SmsTmpSelect, SelectUserList},
        data: function() {
            return {
                loading: false,
                form: {
                    sendType: 2, // 1输入手机号发送  2指定用户发送
                    tels: "",
                    params: [],
                },
                params:[],
                smsYvlan: '',
                tmp: {},
                users: [],
                userNum: 0,
            };
        },
        created() {
        },
        methods: {
            selecrTmp(val) {
                this.smsYvlan = val.text;
                this.tmp = val;
            },
            inputTel(val) {
                var aa = this.form.tels.split("\r\n");
                if (aa.length > 100) {
                    this.$message({
                        message: '您最多添加100个用户！',
                        type: 'error'
                    });
                    return false;
                }
                this.form.tels = val.toString().replace(/\s/g, "\r\n").replace(/[^\r\n0-9]/g, "");
                var ss = this.form.tels.split("\r\n");
                this.userNum = ss.length;
            },
            onSubmit() {
                this.loading = true;
                var  userIds = [];
                let mobiles = [];
                if (this.form.sendType == 2) {
                    this.users.forEach(res => {
                        mobiles.push(res.mobile);
                    });
                }else{
                    mobiles = this.form.tels.split("\r\n");
                }
                api.post("/shop/admin.marketing.sms/senSms", {
                    appToken: this.$store.getters.appToken,
                    k: this.tmp.id,
                    userIds:userIds,
                    mobile:mobiles,
                    params:this.tmp.params,
                }).then(()=> {
                    this.$message({
                        message:'发送成功',
                        type:'success',
                    });
                    this.tels = [];
                    this.tmp = {};
                    this.users = [];
                    this.params = [];
                    this.loading = false;
                }).catch(() => {
                    this.loading= false;
                });
            },
            deleteUser(index) {
                this.users.splice(index, 1);
                this.userNum = this.users.length;
            },
            selectUser(val) {
                if ((+this.users.length + +val.length) > 100) {
                    this.$message({
                        message: '您最多添加100个用户！',
                        type: 'error'
                    });
                    return false;
                }
                val.forEach(row => {
                    this.users.unshift(row);
                });
                let newUsers = [];
                this.users = this.users.reduce(function (item, next) {
                    newUsers[next.user_id] ? '' : newUsers[next.user_id] = true && item.push(next);
                    return item;
                }, []);
                this.userNum = this.users.length;
            },
        }
    }
</script>




<style scoped>

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

</style>
