<template>
    <div>
        <el-button @click="addUser" type="text">添加用户</el-button>

        <el-dialog title="选择用户" :visible.sync="dialogTableVisible" width="80%" :modal-append-to-body="false">
            <el-form :inline="true" :model="search" class="search-form" >
                <!--<el-form-item label="注册开始时间">-->
                    <!--<el-date-picker v-model="search.bgTime" type="datetime" placeholder="选择日期时间"></el-date-picker>-->
                <!--</el-form-item>-->

                <!--<el-form-item label="注册结束时间">-->
                    <!--<el-date-picker v-model="search.endTime" type="datetime" placeholder="选择日期时间"></el-date-picker>-->
                <!--</el-form-item>-->

                <!--<el-form-item label="最近登录开始时间">-->
                    <!--<el-date-picker v-model="search.bgLoginTime" type="datetime" placeholder="选择日期时间"></el-date-picker>-->
                <!--</el-form-item>-->

                <!--<el-form-item label="最近登录结束时间">-->
                    <!--<el-date-picker v-model="search.endLoginTime" type="datetime" placeholder="选择日期时间"></el-date-picker>-->
                <!--</el-form-item>-->

                <!--<br/>-->

                <el-form-item label="关键字">
                    <el-input size="small" v-model="search.name" placeholder="手机或者昵称"></el-input>
                </el-form-item>
                <el-form-item label="会员组">
                    <el-select size="small" v-model="search.vip" placeholder="选择会员组">
                        <el-option label="全部状态" :value="-999"></el-option>
                        <el-option label="普通用户" :value="0"></el-option>
                        <el-option label="PLUS会员" :value="1"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="用户状态">
                    <el-select size="small" v-model="search.is_lock" placeholder="选择状态">
                        <el-option label="全部状态" :value="-999"></el-option>
                        <el-option label="锁定" :value="1"></el-option>
                        <el-option label="正常" :value="0"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button size="small" @click="searchDatas" type="primary">搜索</el-button>&nbsp;&nbsp;
                    <el-button size="small" @click="search.name='',search.vip=-999,search.bgTime='',search.endTime='',
                    search.bgLoginTime='',search.endLoginTime='',search.is_lock=-999,searchDatas(0)" type="danger">取消搜索</el-button>
                </el-form-item>
            </el-form>

            <el-table :data="userList" size="mini" ref="multipleTable"
                      @selection-change="handleSelectionChange" row-key="user_id">
                <el-table-column type="selection" :reserve-selection="true" prop="user_id" width="55"></el-table-column>

                <el-table-column width="100" prop="user_id" label="用户ID"></el-table-column>

                <el-table-column label="头像" width="100">
                    <template slot-scope="scope">
                        <img width="30" :src="scope.row.face"/>
                    </template>
                </el-table-column>

                <el-table-column prop="nickname" label="用户昵称"></el-table-column>

                <el-table-column prop="mobile" label="手机号"></el-table-column>

                <el-table-column label="会员组" width="260">
                    <template slot-scope="scope">
                        <p>{{scope.row.vip ? 'PLUS会员' : '普通用户'}}</p>
                        <p v-if="scope.row.vip > 0">会员到期:<span>{{scope.row.vip_time}}</span></p>
                    </template>
                </el-table-column>

                <el-table-column label="明细">
                    <template slot-scope="scope">
                        <p>余额：<span style="color: red;">￥{{scope.row.money}}</span></p>
                        <p>订单数：<span>{{scope.row.order_num}}</span></p>
                    </template>
                </el-table-column>

                <el-table-column width="310" label="注册/最后登录">
                    <template slot-scope="scope">
                        <p>{{scope.row.reg_time}}</p>
                        <p>{{scope.row.last_time}}</p>
                    </template>
                </el-table-column>

                <el-table-column label="状态">
                    <template slot-scope="scope">
                        <p v-if="scope.row.is_lock ==1" style="color:red;">冻结中</p>
                        <p v-if="scope.row.is_lock ==0">正常</p>
                    </template>
                </el-table-column>
            </el-table>
            <div class="list-btn">
                <div class="lt">
                    <el-button @click="selectUser"  size="small" plain>确认选择</el-button>
                </div>
                <div class="rt">
                    <el-pagination @current-change="setPage" :page-size="limit" layout="total, prev, pager, next" :total="count"></el-pagination>
                </div>
            </div>

        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/api.js';

    export default {
        name: "SelectUserList",
        data() {
            return {
                dialogTableVisible: false,
                search: {
                    keyword: '',
                    status: 0,
                    bgTime: '',
                    endTime: '',
                    vipGroup: 0,
                },
                page: 1,
                limit: 10,
                count: 0,
                loading: false,
                datas: [],
                selectUserList: [],
                userList: [],
            }
        },
        created() {
            this.getUserList();
        },
        methods: {
            addUser(){
                this.dialogTableVisible = true;
                if(this.$refs.multipleTable){
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(rows) {
                this.selectUserList = rows;
            },
            selectUser(){
                this.dialogTableVisible = false;
                this.$emit('select-user',  this.selectUserList);
            },
            setPage(res) {
                this.page = res;
                this.getUserList();
            },
            searchDatas() {
                this.page = 0;
                this.getUserList();
            },
            getUserList() {
                var that = this;
                that.loading = true;
                api.post("/shop/admin.user.user/getUserList", {
                    appToken: that.$store.getters.appToken,
                    keyword: that.search.keyword,
                    status: that.search.status,
                    bgTime: that.search.bgTime,
                    endTime: that.search.endTime,
                    vip: that.search.vip,
                    page: this.page,
                    limit: 10,
                }).then(res => {
                    that.userList = res.list;
                    that.count = res.count;
                    that.limit = res.limit;
                    that.loading = false;
                }).catch((error) => {
                    that.loading = false;
                });
            },
        },
    }
</script>

<style scoped>

</style>
