<template>
    <div>
        <el-button type="text" @click="dialogTableVisible = true">选择模板</el-button>
        <el-dialog title="选择模板" :visible.sync="dialogTableVisible" width="80%" :modal-append-to-body="false">
            <el-table  :data="tmpList.list" style="width: 100%;margin-top: -40px;" size="mini">
                <el-table-column label="模板简介" prop="title" width="180"></el-table-column>

                <el-table-column label="模板内容" prop="text"></el-table-column>

                <el-table-column label="操作" width="60">
                    <template slot-scope="scope">
                            <el-button @click="selectTmp(scope)" type="text">选择</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="list-btn">
                <div class="rt">
                    <el-pagination @current-change="getIndex" :page-size="tmpList.limit" layout="total, prev, pager, next" :total="tmpList.count"></el-pagination>
                </div>
            </div>
        </el-dialog>
    </div>

</template>

<script>
    import * as api from "@/api.js";
    export default {
        name: "SmsTmpSelect",
        data(){
            return {
                dialogTableVisible: false,
                tmpList:[],
            }
        },
        created(){
            this.getIndex();
        },
        methods:{
            getIndex(page = 1){
                api.post("/shop/admin.marketing.sms/getIndex", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                }).then(res => {
                    this.tmpList = res;
                }).catch((error) => {

                });
            },
            selectTmp(val){
                this.dialogTableVisible = false;
                this.$emit('select-tmp', val.row);
            },
        },

    }
</script>

<style scoped>

</style>
